<script>
  import { fade, scale } from "svelte/transition";
  import { data, page, setting } from "../store";
  import collection from "../data/collection.json";
  import equipment from "../data/equipment.json";
  import souvenir from "../data/souvenir.json";
  import spellcard from "../data/spellcard.json";
  import role from "../data/role.json";
  import words from "../data/words.json";
  import growth from "../data/growth";
  import { Explain } from "../addon/base";

  export const menu = menuHandle;
  let enable = false;
  let index = "collection";
  let explain;

  let txt1 = random();
  let txt2 = random();
  let txt3 = random();
  let txt4 = random();
  let txt5 = random();
  let txt6 = random();
  let txt7 = random();
  let txt8 = random();
  let txt9 = random();
  let txt0 = random();
  setTimeout((_) => setInterval((_) => (txt1 = random()), 2000), 200 * 0);
  setTimeout((_) => setInterval((_) => (txt2 = random()), 2000), 200 * 1);
  setTimeout((_) => setInterval((_) => (txt3 = random()), 2000), 200 * 2);
  setTimeout((_) => setInterval((_) => (txt4 = random()), 2000), 200 * 3);
  setTimeout((_) => setInterval((_) => (txt5 = random()), 2000), 200 * 4);
  setTimeout((_) => setInterval((_) => (txt6 = random()), 2000), 200 * 5);
  setTimeout((_) => setInterval((_) => (txt7 = random()), 2000), 200 * 6);
  setTimeout((_) => setInterval((_) => (txt8 = random()), 2000), 200 * 7);
  setTimeout((_) => setInterval((_) => (txt9 = random()), 2000), 200 * 8);
  setTimeout((_) => setInterval((_) => (txt0 = random()), 2000), 200 * 9);

  const position = [
    {
      x: 50,
      y: 0,
    },
    {
      x: 35.4,
      y: -35.4,
    },
    {
      x: 0,
      y: -50,
    },
    {
      x: -35.4,
      y: -35.4,
    },
    {
      x: -50,
      y: 0,
    },
    {
      x: -35.4,
      y: 35.4,
    },
    {
      x: 0,
      y: 50,
    },
    {
      x: 35.4,
      y: 35.4,
    },
  ];
  let State = growth.role[$data.role]($data.lv);
  const state = [
    {
      type: "maxHealth",
      name: "最大生命值",
      detail: "显而易见，生命值不会超过这个数。",
      data: State.health,
    },
    {
      type: "attack",
      name: "攻击力",
      detail: "所有攻击都以这个数为基础。不过也有例外。",
      data: State.attack,
    },
    {
      type: "speed",
      name: "敏捷",
      detail: "决定了回合中行动的自由度，速度快，能做的事也就会更多呢。",
      data: State.speed,
    },
    {
      type: "power",
      name: "灵力",
      detail: "使用普通攻击要消耗1灵力，符卡则消耗3，每回合初都会回复至上限。",
      data: State.power,
    },
    {
      type: "health",
      name: "生命值",
      detail: "必须不让它归零才行。",
      data: $data.health,
    },
    {
      type: "sugar",
      name: "糖",
      detail: "面临选择时可用选项的数量。",
      data: $data.sugar,
    },
    {
      type: "level",
      name: "等级",
      detail: "等级越高越强大，只能通过战斗提升等级。",
      data: $data.lv,
    },
    {
      type: "chance",
      name: "残机",
      detail: "战败时消耗1残机回溯到战斗前，回复全部生命值并获得补给。",
      data: $data.chance,
    },
  ];

  function menuHandle(e) {
    console.log(e);
    e.preventDefault();
    if ($page != "Index") enable = !enable;
  }
  function random() {
    return {
      x: `${-40 - 160 * r()}px`,
      y: `${80 - 160 * r()}px`,
      word: words[Math.floor(r() * words.length)],
    };
  }
  function r() {
    return Math.random();
  }
</script>

<div class="body {enable && 'menu'} {$data.role}">
  <Explain bind:explain />
  <div class="words">
    {#each [txt1, txt2, txt3, txt4, txt5, txt6, txt7, txt8, txt9, txt0] as p, i (i)}
      <txt
        style="left:{p.x};top:{p.y};font-size:{26 -
          r() * 8}px;animation-delay:{i * 0.2}s"
      >
        {p.word}
      </txt>
    {/each}
  </div>
  <div class="info" id="info">
    <div class="title">
      <img src="/svg/data.svg" />
      <txt>思与想的境界</txt>
    </div>
    <div class="state">
      {#each state as s, i (i)}
        <div
          on:mouseenter={(_) => explain.show()}
          on:mousemove={(e) => explain.move(e, s, "blue")}
          on:mouseleave={(_) => explain.hide()}
        >
          <img
            style="left:{position[i].x}px;top:{position[i].y}px;"
            src="/svg/{s.type}.svg"
          />
          <txt style="left:{position[i].x * 1.8}px;top:{position[i].y * 1.8}px;"
            >{s.data}</txt
          >
        </div>
      {/each}
    </div>
  </div>
  <div
    class="role"
    on:mouseenter={(_) => explain.show()}
    on:mousemove={(e) => explain.move(e, role[$data.role], "blue")}
    on:mouseleave={(_) => explain.hide()}
  >
    <img src="/img/role/{$setting.resource}/{$data.role}.webp" />
  </div>
  <div class="item">
    <div class="index">
      <txt
        class={index == "collection" && "selected"}
        on:click={(_) => (index = "collection")}>收藏品</txt
      >
      <txt
        class={index == "spellcards" && "selected"}
        on:click={(_) => (index = "spellcards")}>符卡</txt
      >
      <txt
        class={index == "consumable" && "selected"}
        on:click={(_) => (index = "consumable")}>消耗品</txt
      >
    </div>
    {#if index == "collection"}
      <div
        class="collection"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each Object.keys($data.collections) as c, i (i)}
          <div
            on:mouseenter={(_) => explain.show()}
            on:mousemove={(e) =>
              explain.move(e, collection[c], collection[c].type)}
            on:mouseleave={(_) => explain.hide()}
          >
            <img src="/svg/collection/{c}.svg" />
            {#if Object.values($data.collections)[i] > 1}
              <txt>{Object.values($data.collections)[i]}</txt>
            {/if}
          </div>
        {/each}
      </div>
    {/if}
    {#if index == "spellcards"}
      <div
        class="spellcards"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each Object.keys($data.spellcards) as s, i (i)}
          <div
            class="spellcard"
            on:mouseenter={(_) => explain.show()}
            on:mousemove={(e) => explain.move(e, spellcard[s], "blue")}
            on:mouseleave={(_) => explain.hide()}
          >
            <img src="/svg/spellcard/{s}.svg" />
            {#if Object.values($data.spellcards)[i] > 1}
              <txt>{Object.values($data.spellcards)[i]}</txt>
            {/if}
          </div>
        {/each}
      </div>
    {/if}
    {#if index == "consumable"}
      <div
        class="consumable"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      />
    {/if}
  </div>
  <div class="action">
    <txt on:click={_ => {
      //$page = "Index";
      enable = false;
    }}>标题界面</txt>
  </div>
</div>

<style>
  .body {
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
    opacity: 0;
    backdrop-filter: blur(16px);
    transition: 0.4s;
  }

  .words {
    position: relative;
    align-items: center;
    justify-content: center;
  }
  .words txt {
    color: white;
    font-size: 24px;
    position: absolute;
    width: 240px;
    text-align: center;
    animation: word 2s infinite;
    text-shadow: 0 0 1px #666, 0 0 1px #666, 0 0 1px #666, 0 0 1px #666,
      0 0 1px #666, 0 0 1px #666, 0 0 1px #666;
  }

  .menu {
    z-index: 8;
    opacity: 1;
  }

  .info {
    position: absolute;
    left: 0;
    top: 0;
  }

  .title {
    flex-direction: row;
    margin: 5px;
  }

  .title > img {
    width: 70px;
  }
  .title > txt {
    font-size: 32px;
    color: #555;
    height: 70px;
    line-height: 70px;
    margin-left: 10px;
    font-family: TiejiliSC;
    color: white;
    text-shadow: 0 0 3px #666, 0 0 3px #666, 0 0 3px #666, 0 0 3px #666,
      0 0 3px #666, 0 0 3px #666, 0 0 3px #666;
  }

  .state {
    margin-left: 120px;
    margin-top: 100px;
    position: relative;
  }
  .state > div > img {
    width: 32px;
    position: absolute;
  }
  .state > div > txt {
    line-height: 32px;
    font-size: 18px;
    font-family: TiejiliSC;
    color: white;
    text-shadow: 0 0 3px #666, 0 0 3px #666, 0 0 3px #666, 0 0 3px #666,
      0 0 3px #666, 0 0 3px #666, 0 0 3px #666;
    position: absolute;
    text-align: center;
    width: 32px;
  }
  :global(.state > div:hover > .explain) {
    z-index: 5;
    opacity: 1;
    visibility: visible;
  }
  :global(.state > div > .explain:hover) {
    opacity: 0;
    visibility: hidden;
  }

  .role {
    position: absolute;
    bottom: 0;
    left: 0;
  }
  .role > img {
    width: 240px;
  }

  .item {
    position: absolute;
    right: 0;
    top: 0;
  }
  .item > div {
    flex-direction: row;
  }
  .index > txt {
    width: 110px;
    line-height: 40px;
    margin: 5px;
    background-color: #63636369;
    border: 3px solid #ddd;
    font-size: 20px;
    color: white;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
    font-family: SanJiSuXianJianTi;
    transition: 0.2s;
  }
  .index > txt.selected,
  .index > txt:hover {
    background-color: #a1a1a169;
    border-color: white;
    transform: scale(1.05);
  }
  .collection,
  .spellcards,
  .consumable {
    position: absolute;
    left: 0;
    top: 60px;
  }
  .spellcards img,
  .collection img {
    margin: auto;
    width: 75%;
    height: 75%;
  }
  .collection > div {
    width: 50px;
    height: 50px;
    border: 3px solid #aaa;
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    position: relative;
    cursor: pointer;
    transition: 0.2s;
  }
  .collection txt {
    font-size: 16px;
    position: absolute;
    right: -8px;
    bottom: -8px;
    font-family: TiejiliSC;
    color: white;
    text-shadow: 0 0 3px #666, 0 0 3px #666, 0 0 3px #666, 0 0 3px #666,
      0 0 3px #666, 0 0 3px #666, 0 0 3px #666;
  }
  .collection > div:hover {
    border-color: white;
    transform: scale(1.1);
  }
  .spellcard {
    width: 110px;
    height: 110px;
    border-radius: 12px;
    border: 4px solid white;
    filter: drop-shadow(0 0 6px #666);
    cursor: pointer;
    margin: 5px;
    box-sizing: border-box;
    transition: 0.2s;
  }
  .spellcard txt {
    font-size: 24px;
    position: absolute;
    right: -12px;
    bottom: -12px;
    font-family: TiejiliSC;
    color: white;
    text-shadow: 0 0 4px #666, 0 0 4px #666, 0 0 4px #666, 0 0 4px #666,
      0 0 4px #666, 0 0 4px #666, 0 0 4px #666;
  }
  .spellcard:hover {
    filter: drop-shadow(0 0 6px #fff);
  }

  .action {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 20px;
    align-items: center;
  }
  .action > txt {
    cursor: pointer;
    transition: 0.2s;
    opacity: 0.6;
    font-size: 30px;
    color: white;
    font-family: SanJiSuXianJianTi;
    line-height: 50px;
    text-shadow: 0 0 3px #888, 0 0 3px #888, 0 0 3px #888, 0 0 3px #888,
      0 0 3px #888, 0 0 3px #888, 0 0 3px #888;
  }
  .action > txt:hover {
    transform: scale(1.1);
    opacity: 1;
  }
  .action > txt:active {
    text-shadow: 0 0 3px #fff, 0 0 3px #fff, 0 0 3px #fff, 0 0 3px #fff,
      0 0 3px #fff, 0 0 3px #fff, 0 0 3px #fff;
  }

  @keyframes word {
    0% {
      opacity: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }
</style>
